/* styles.css */
body {
    font-family: 'Arial', sans-serif;
    background-color: #f8f9fa;
    margin: 0;
    padding: 0;
}

.container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background: white;
}

.header {
    background-color: #ff7e36;
    color: white;
    padding: 15px;
    font-size: 20px;
    text-align: center;
    font-weight: bold;
}

.product-list {
    list-style: none;
    padding: 0;
}

.product-item {
    display: flex;
    align-items: center;
    padding: 15px;
    border-bottom: 1px solid #ddd;
    background: white;
}

.product-item img {
    width: 80px;
    height: 80px;
    border-radius: 10px;
    margin-right: 15px;
}

.product-details {
    flex: 1;
}

.product-name {
    font-size: 16px;
    font-weight: bold;
}

.product-price {
    color: #ff7e36;
    font-weight: bold;
}

.product-location {
    color: #777;
    font-size: 12px;
}
